import { ModuleWithProviders, NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { provideRoutes, RouterModule, Routes } from '@angular/router';
import { ContactManagerJourneyConfigurationService } from './services/contact-manager-journey-config.service';
import { contactManagerRoutes } from './contact-manager-journey.routes';
import { ContactManagerWidgetModule } from '@backbase/contact-manager-widget-ang';
import { ContactManagerJourneyComponent } from './contact-manager-journey.component';
import { ContactManagerDetailViewComponent } from './views/contact-manager-detail/contact-manager-detail.component';
import { ContactManagerTypeListViewComponent } from './views/contact-manager-type-list/contact-manager-type-list.component';
import { ContactManagerFormViewComponent } from './views/contact-manager-form/contact-manager-form.component';
import { HeaderModule } from '@backbase/ui-ang/header';

/**
 * Journey for contacts in retails apps
 *
 * @usageNotes
 *
 * ### Include the exported widgets in your `AppModule`:
 *
 * ```typescript
 * export const routes: Routes = [
 *   {
 *     path: '',
 *     component: ContactManagerJourneyComponent,
 *     children: [
 *       {
 *         path: '',
 *         component: ContactManagerDetailViewComponent,
 *       },
 *       {
 *         path: 'select',
 *         component: ContactManagerTypeListViewComponent,
 *       },
 *       {
 *         path: ':id',
 *         component: ContactManagerDetailViewComponent,
 *       },
 *       {
 *         path: 'edit/:id/:type',
 *         canDeactivate: [CanDeactivateGuard],
 *         component: ContactManagerFormViewComponent,
 *       },
 *     ],
 *   },
 * ];
 * };
 * @NgModule({
 * imports: [ContactManagerJourneyModule.forRoot({ route })],
 *  providers: [ {
 *   provide: ContactManagerJourneyConfigurationToken,
 *   useValue: {
 *     createContactEmail: true,
 *     createContactIBAN: true,
 *     createContactAccount: true,
 *     createContactPhone: true,
 *   } as Partial<ContactManagerJourneyConfig>,
 * },],
 * })
 * export class AppModule {}
 * ```
 */
@NgModule({
  declarations: [
    ContactManagerJourneyComponent,
    ContactManagerDetailViewComponent,
    ContactManagerTypeListViewComponent,
    ContactManagerFormViewComponent,
  ],
  imports: [CommonModule, ContactManagerWidgetModule, HeaderModule, RouterModule.forChild(contactManagerRoutes)],
  providers: [ContactManagerJourneyConfigurationService],
})
export class ContactManagerJourneyModule {
  static forRoot(
    data: { routes: Routes; [key: string]: any } = { routes: contactManagerRoutes },
  ): ModuleWithProviders<ContactManagerJourneyModule> {
    return {
      ngModule: ContactManagerJourneyModule,
      providers: [provideRoutes(data.routes)],
    };
  }
}
